import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import APButton from '../../common/APButton';
import { withRouter } from 'react-router';
import { assignConfigWithLocal } from '../../../config';
import { AssignRoutes } from '../../../routeTemplates';
import path from '../../../helpers/path';
import APSelect from '../../common/APSelect/APSelect';
import APIcon from '../../common/APIcon/APIcon';
import ContentWithHeaderLayoutTemplate from '../../common/LayoutTemplates/ContentWithHeaderLayoutTemplate/ContentWithHeaderLayoutTemplate';

import './DemoSettingsEditableComponent.css';

const demoSettingsClasses = getBEMClasses('demo-settings');

const demoSettingsWidgetsClasses = getBEMClasses('demo-settings-widget');

class DemoSettingsComponent extends React.Component {
  static contextTypes = {
    t: PropTypes.func.isRequired
  };

  componentDidMount() {
    const { formValues, change } = this.props;
    const fieldsOptions = {
      template: this.getTemplateOptions(),
      theme: this.getThemeOptions(),
      chart: this.getChartOptions()
    };

    Object.keys(fieldsOptions).forEach(field => {
      if (!formValues[field]) {
        const { value } = fieldsOptions[field][0];
        change(field, value);
      }
    });
  }

  handleSubmit = event => {
    const { formValues } = this.props;
    localStorage.setItem('apexTemplate', JSON.stringify(formValues));
    event.preventDefault();
    this.reloadConfigAndRefresh();
  };

  clearApexTemplateInLocalStorage = event => {
    localStorage.setItem('apexTemplate', JSON.stringify({}));
    event.preventDefault();
    this.reloadConfigAndRefresh();
  };

  reloadConfigAndRefresh = () => {
    assignConfigWithLocal();
    AssignRoutes();
    this.props.history.push(path('/login'));
    window.location.reload();
  };

  getLayoutHeader = () => {
    const { goToStep } = this.props;
    return (
      <div className={demoSettingsWidgetsClasses('header')}>
        <div className={demoSettingsWidgetsClasses('header-text')}>
          {this.context.t('Edit Settings')}
        </div>
        <div onClick={() => goToStep(0)}>
          <APIcon
            name="close"
            customClass={demoSettingsWidgetsClasses('icon')}
            classModifiers="big"
          />
        </div>
      </div>
    );
  };

  getTemplateOptions = () => [
    { value: 'NEWYORK', label: 'New York' },
    { value: 'MILAN', label: 'Milan' },
    { value: 'LONDON', label: 'London' }
  ];

  getThemeOptions = () => [
    { value: 'dark', label: 'Dark' },
    { value: 'light', label: 'Light' }
  ];

  getChartOptions = () => [
    { value: 'APEX', label: 'APEX' },
    { value: 'TradingView', label: 'TradingView' }
  ];

  render() {
    return (
      <div className={demoSettingsClasses()}>
        <ContentWithHeaderLayoutTemplate
          header={this.getLayoutHeader()}
          customClass={demoSettingsWidgetsClasses()}>
          <React.Fragment>
            <form className={demoSettingsClasses('form')}>
              <div className={demoSettingsClasses('content-holder')}>
                <div className={demoSettingsClasses('row')}>
                  <APSelect
                    name="template"
                    label={this.context.t('Template')}
                    customClass={demoSettingsClasses()}
                    options={this.getTemplateOptions()}
                  />
                </div>

                <div className={demoSettingsClasses('row')}>
                  <APSelect
                    name="theme"
                    label={this.context.t('Color Theme')}
                    customClass={demoSettingsClasses()}
                    options={this.getThemeOptions()}
                  />
                </div>

                <div className={demoSettingsClasses('row')}>
                  <APSelect
                    name="chart"
                    label={this.context.t('Chart Type')}
                    customClass={demoSettingsClasses()}
                    options={this.getChartOptions()}
                  />
                </div>

                <div className={demoSettingsClasses('row')}>
                  <APButton
                    customClass={demoSettingsClasses()}
                    onClick={this.handleSubmit}>
                    Apply
                  </APButton>
                  <APButton
                    customClass={demoSettingsClasses()}
                    onClick={this.clearApexTemplateInLocalStorage}>
                    Clear
                  </APButton>
                </div>
              </div>
            </form>
          </React.Fragment>
        </ContentWithHeaderLayoutTemplate>
      </div>
    );
  }
}

export default withRouter(DemoSettingsComponent);



